import React from 'react'
import './components.css'

const Input = ({value, onChange}) => {
  return(
    <input
      className='fas fa-eye-dropper'
      type='color'
      value={value}
      onChange={onChange}
    />
  )
}

const Color = ({color, onClick, id}) => {
  return(
    <td  
      className='color'
      style={{backgroundColor: color}}
      onClick={onClick}
      id={id}
    />
  )
}

const Palette = ({onClick, changeExistingColor, newColor, defaultColors}) => {
  return(
    <div className='Palette'>
      <table className='colors'>
        <tbody>
          {defaultColors.map((colors, i) => 
            <tr key={`pallete row ${i}`}>
              {colors.map((color, j) =>
                <Color
                  key={`color ${i}${j}`}
                  color={color}
                  id={`color ${i}${j}`}
                  onClick={onClick}
                />
              )}
            </tr>
          )}
        </tbody>
      </table>
    
      <Input
        value={newColor}
        onChange={changeExistingColor}
      />
    </div>
  )
}

export default Palette
