import React from 'react'
import './components.css'

const Input = ({value, onChange}) => {
  return(    
    <input 
      type='number'
      min='1'
      max='32'
      step='1'
      className='input'
      value={value}
      onChange={onChange}
    />
  )
}
  
const DimensionSelector = ({newDimensions, onSubmit, changeHeight, changeWidth}) => {
  return(
    <form onSubmit={onSubmit}>
      <button 
        type='submit'
        className='button'  
      >
        Change Dimensions
      </button>
      <Input 
        value={newDimensions.height} 
        onChange={changeHeight}
      />
      <Input 
        value={newDimensions.width} 
        onChange={changeWidth}
      />
    </form>
  )
}

export default DimensionSelector