import React from 'react'
import './components.css'

const Pixel = ({color, className}) => {
  return(
    <div 
      className={className} 
      onMouseDown={(event) => {
        event.target.style.backgroundColor = color
      }}
    />
  )
}

const Grid = ({dimensions, color, showGrid}) => {
  return(
    <div className='gridArea'>
      <table className={showGrid? 'grid': 'noGrid'}>
        <tbody>
          {[...Array(dimensions.height)].map((_, i) => 
            <tr key={`row ${i}`}>
              {[...Array(dimensions.width)].map((_, i) => 
                <td key={`column ${i}`}>
                  <Pixel 
                    color={color}
                    className={showGrid? 'pixel': 'pixelNoGrid'}
                  />
                </td>
              )}
            </tr>
          )}
        </tbody>
      </table>
    </div>
  )
}

export default Grid