import React from 'react'
import './components.css'

const Button = ({onClick, text}) => {
  return(
    <div>
      <button 
        className='button'
        onClick={onClick} 
      >
        {text}
      </button>
    </div>
  )
}

const Settings = ({showGrid, setShowGrid}) => {
  return(
    <div className='settings'>
      <Button
        text={'Toggle Grid'}
        onClick={() => setShowGrid(!showGrid)}
      />
      <Button
        text={'Clear Grid'}
        onClick={() => {
          let pixels = showGrid?
            document.querySelectorAll('.pixel'):
            document.querySelectorAll('.pixelNoGrid')

          pixels.forEach(pixel => 
            pixel.style.backgroundColor = '#414141'
          )
        }}
      />
    </div>
  )
}

export default Settings
