import {React, useState} from 'react'
import Grid from './components/Grid'
import DimensionSelector from './components/DimensionSelector'
import Palette from './components/Palette'
import Settings from './components/Settings'

const App = () => {
  const [id, setId] = useState('color 11')
  const [color, setColor] = useState('#FFFFFF')
  const [dimensions, setDimensions] = useState({height: 16, width: 16})
  const [newDimensions, setNewDimensions] = useState({height: 1, width: 1})
  const [showGrid, setShowGrid] = useState(true)
  const [colors, setColors] = useState([
    ['#FF0000','#FF5500','#FFFF00','#00FF00','#0000FF','#5500FF','#FF00FF','#FFFFFF'],
    ['#990000','#993300','#999900','#009900','#000099','#330099','#990099','#999999'],
    ['#660000','#661100','#666600','#006600','#000066','#110066','#660066','#666666']
  ])

  const changeDimensions = (event) => {
    event.preventDefault()
    setDimensions(newDimensions)
  }

  const changeHeight = (event) => {
    setNewDimensions({
      height: parseInt(event.target.value),
      width: newDimensions.width
    })
  }

  const changeWidth = (event) => {
    setNewDimensions({
      height: newDimensions.height,
      width: parseInt(event.target.value)
    })
  }

  const changeColor = (event) => {
    setId(event.target.id)
    setColor(event.target.style.backgroundColor)
  }

  const changeExistingColor = (event) => {
    setColor(event.target.value)
    document.getElementById(id).style.backgroundColor = event.target.value
  }

  return(
    <div className='App'>
      
      <div className='title'>
        <h1> Free Pixel Art </h1>
        <h5> By: Jake Marion </h5>
      </div>

      <div className='container'>
        <Grid 
          dimensions={dimensions} 
          color={color} 
          showGrid={showGrid}
        />
        <Settings 
          showGrid={showGrid}
          setShowGrid={setShowGrid}
        />
      </div>

      <DimensionSelector
        newDimensions={newDimensions}
        onSubmit={changeDimensions}
        changeHeight={changeHeight}
        changeWidth={changeWidth}
      />

      <div>
        <Palette
          defaultColors={colors}
          newColor={color}
          onClick={changeColor}
          setId={setId}
          changeExistingColor={changeExistingColor}
        />
      </div>
    </div>
  )
}

export default App;
